import React from "react";
import { HashRouter as Router, Route, Link } from "react-router-dom";
import { Flex, ThemeProvider, Text } from "@chakra-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import IconButton from "@material-ui/core/IconButton";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import ArrowForwardIcon from "@material-ui/icons/ArrowForward";
import Stepper from "@material-ui/core/Stepper";
import Step from "@material-ui/core/Step";
import StepLabel from "@material-ui/core/StepLabel";


import "./background.css";
import Background from './global-outlook-power-ban-cover.jpeg';
import logo from './USC_logo.png';
import LineChart from "./charts/line";
import Bubble from "./charts/bubble";
import Bubble2 from "./charts/bubble2";
import MapBox from "./charts/mapbox";
import "./charts/mapbox.css";
import Pie from "./charts/pie.js";
import Pie2 from "./charts/pie2.js";
import "./charts/pie.css";
import Bar from "./charts/bar.js";
import "./charts/bar.css";
import MapChart from "./charts/geomap3.js";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
  },
  icon: {
    color: "#76777B",
    "&$activeIcon": {
      color: "#990000",
    },
    "&$completedIcon": {
      color: "#009900",
    },
  },
  activeIcon: {},
  completedIcon: {},
  steper: {
    width: "80%",
    alignItems: "center",
    justify: "center",
  },
  LeftButton: {
    marginTop: theme.spacing(-21),
    marginLeft: "1%",
    "& svg": {
      fontSize: 50,
      fill: "#990000",
    },
  },
  RightButton: {
    marginTop: theme.spacing(-25),
    marginLeft: "95%",
    "& svg": {
      fontSize: 50,
      fill: "#990000",
    },
  },
  ResetButton: {
    marginTop: theme.spacing(-25),
    marginLeft: "95%",
  },
  MemberButton: {
    marginTop: theme.spacing(0),
    marginLeft: "5%",
    marginRight: "5%"
  }
}));

function lists() {
  return [
    "Homepage",
    "Multi-Line Chart",
    "Bar Chart",
    "Pie Chart",
    "Bubble Pack",
    "MapBox",
    "Geographical Map"
  ];
}

function App() {
  const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(0);
  const ls = lists();

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };

  const myFunction = () => {
    var text = document.getElementById("demo");
    if (text.style.display === "none") {
      text.style.display = "block";
    } else {
      text.style.display = "none";
    }

    var text2 = document.getElementById("demo2");
    if (text2.style.display === "none") {
      text2.style.display = "block";
    } else {
      text2.style.display = "none";
    }

    var text3 = document.getElementById("demo3");
    if (text3.style.display === "none") {
      text3.style.display = "block";
    } else {
      text3.style.display = "none";
    }

    var text4 = document.getElementById("demo4");
    if (text4.style.display === "none") {
      text4.style.display = "block";
    } else {
      text4.style.display = "none";
    }
  }

  return (
    <ThemeProvider>
      <Flex direction="column">
        <header class="site-header">
          <div class="site-identity">
            <img src = {logo} width="100%" height="%" alt='logo'/>
            <h1>Nuclear Notebook - Team Army</h1>     
            <Button
              className={classes.MemberButton}
              onClick={myFunction}
              style={{
                color: "#FFC72C"
              }}>
             Click to See Team Member
            </Button>
             
          </div>
          <div class="site-navigation">
          <ul>
           <p id='demo' style={{display: 'none', color: '#FFC72C'}}>Jinglun Chen (jinglunc@usc.edu)</p>
           <p id='demo2' style={{display: 'none', color: '#FFC72C'}}>Yi Jin (yjin8395@usc.edu)</p>
           <p id='demo3' style={{display: 'none', color: '#FFC72C'}}>Yu Ren (yuren@usc.edu)</p>
           <p id='demo4' style={{display: 'none', color: '#FFC72C'}}>Zhun Wei (zhunwei@usc.edu)</p>
          </ul>
          </div>
        </header>
        <Stepper
          className={classes.stepper}
          activeStep={activeStep}
          alternativeLabel
        >
          {ls.map((label) => (
            <Step key={label}>
              <StepLabel
                StepIconProps={{
                  classes: {
                    root: classes.icon,
                    active: classes.activeIcon,
                    completed: classes.completedIcon,
                  },
                }}
              >
                {label}
              </StepLabel>
            </Step>
          ))}
        </Stepper>
        <div>
          <IconButton
            aria-label="left"
            onClick={activeStep !== 0 ? handleBack : null}
            className={classes.LeftButton}
          >
            <ArrowBackIcon />
          </IconButton>
          {activeStep !== 6 && (
            <IconButton
              aria-label="right"
              onClick={activeStep !== 6 ? handleNext : null}
              className={classes.RightButton}
            >
              <ArrowForwardIcon />
            </IconButton>
          )}
          {activeStep === 6 && (
            <Button
              className={classes.ResetButton}
              onClick={handleReset}
              style={{
                backgroundColor: "#990000",
                color: "#FFC72C"
              }}
            >
              Reset
            </Button>
          )}
        </div>

        {activeStep === 0 && (
          <Flex mr={120} ml={120} mt={-4}>
          <div class="container">
            <img src = {Background} width="100%" height="100%" alt='background'/>
            <div class="centered">Nuclear Notebook</div>
            <div class="overlay">
            <div class="text">
              <p>
                Despite nuclear weapon deployment has been reduced since the Cold War, the world’s combined inventory of it still remains a high level.</p>
              <p>It is worthwhile studying and discussing on both how the inventories of nuclear warheads change and whether some factors such as the number of deaths from conflict and terrorism, military expense and the era can influence nuclear stockpiles.</p>
              <p>Therefore, we will use multi-party data and visualization to look deeper at this topic.</p>
            </div>
        </div>

        </div>

          </Flex>
        )}
         {activeStep === 3 && (
         <> 
          <Flex mr={120} ml={120} mt={-5}>
            <Flex w="100%" ml={-3}>
              <Text style={{ fontSize: "19px" }}>
                First, the two pie charts respectively show the comparison of the number of nuclear weapons
                stored in different countries in 2014 and 1970. Click the fan-shaped area with the mouse, 
                and the corresponding country name and the number of deposits will be displayed at the top of charts.
                This data is obtained from{" "}
                <a href="https://fas.org/issues/nuclear-weapons/status-world-nuclear-forces/">
                Status of World Nuclear Forces
                </a>
                .<br></br>
                <br></br>
                As can be seen from the graph, in the latest data in 2014, China, France, India, Israel, 
                Pakistan and the United Kingdom have more equal storage numbers, all within 300. 
                The United States and Russia have relatively more storage, exceeding 4,000. In 1970, 
                this trend was even more severe. Other countries had very few stocks. 
                The United States and Russia had almost most nuclear weapons, and the stocks were around 20,000. 
                This is also related to historical factors. The two countries were in the Cold War period.
    
              </Text>
            </Flex>
          </Flex>
          <Flex mr={120} ml={120} mt={-4}>
            <Flex w="50%" mr={10}>
              <Pie></Pie>
            </Flex>
            <Flex w="50%" mr={10}>
              <Pie2></Pie2>
            </Flex>
          </Flex>
        </>
        )}
        {activeStep === 1 && (
          <Flex mr={120} ml={120} mt={-5}>
            <Flex w="20%" ml={-3}>
              <Text style={{ fontSize: "19px" }}>
                Preprocessed the dataset of "Deaths from conflict and terrorism, 1990 to 2017 in the World", 
                we have selected the top 10 countries with the highest number of deaths. The original 
                dataset is obtained from {" "}
                <a href="http://ghdx.healthdata.org/gbd-results-tool"> GBD Results Tool. </a>
                <br></br>
                <br></br>This line chart shows the number of deaths caused by {" "}
                <b>conflict(civil conflict and war between states) and terrorism</b>. We have chosen 10 colors 
                to represent 10 countries. Hover over the colorful dots to see the number of deaths of each year.
              </Text>
            </Flex>
            <Flex w="70%">
              <LineChart></LineChart>
            </Flex>
          </Flex>
        )} 
        {activeStep === 2 && (
         <> 
          <Flex mr={120} ml={120} mt={-5}>
            <Flex w="100%" ml={-3}>
              <Text style={{ fontSize: "19px" }}>
                First, the chart shows the number of deaths due to weapons in war each year. 
                The time span is from 1400 to 2020. The chart provides 6 buttons. By clicking the button, 
                you can observe various sorting and filtering results of the data
                This data is obtained from{" "}
                <a href="https://ourworldindata.org/war-and-peace">
                world conflict deaths(1600 - 2017)
                </a>
                .<br></br>
                <br></br>
                Click All and you can find that the number of deaths due to weapons in the war has increased significantly after 1900, 
                not only because of the scale of the war, but also because of the upgrading of weapons, resulting in an increase in the scale of deaths.
                Click on top 5 by value again to observe the 5 time points with the highest death toll. The First World War occurred in 1915,
                 and 1945 was the Second World War. But nuclear weapons were also used at the end of World War II.
                .<br></br>
                <br></br>
                Click bottom 5 by value, and you can be surprised to find that the number of deaths due to weapons in the war after 1900 was lower than in 1400. 
                This proves that nuclear weapons have a deterrent effect, which has significantly reduced world wars and tended to peace.
              </Text>
            </Flex>
          </Flex>
          <Flex mr={120} ml={120} mt={-4}>
            <Flex w="100%" mr={10}>
              <Bar></Bar>
            </Flex>
            
          </Flex>
        </>
        )}

        {activeStep === 4 && (
         <> 
          <Flex mr={120} ml={120} mt={-5}>
            <Flex w="100%" ml={-3}>
              <Text style={{ fontSize: "19px" }}>
                Firslty, We would like to analyze the impact continents have on nuclear warhead inventories 
                and the types of nuclear warheads countries possess. The large 
                bubbles represent continents of the world. When clicking
                on each bubble reveals the countries within the continents. 
                Clicking on each country shows a comparison of the
                number of different types of nuclear warheads. The nuclear warhead has four types: 
                Deployed Nonstrategic, Deployed Strategic, Reserve/ Nondeployed	and Retired.
                This data is obtained from{" "}
                <a href="https://fas.org/issues/nuclear-weapons/status-world-nuclear-forces/">
                Status of World Nuclear Forces
                </a>
                .<br></br>
                <br></br>
                Then, we created the other zoomable circle packing of current military expenditure (% of GDP) 
                of these countries. In this graph, the large 
                bubbles represent the continents of the world. When clicking
                on each bubble reveals the countries within the contients. 
                Clicking on each country shows the military expenditure (% of GDP).
                This data is obtained from{" "}
                <a href="https://data.worldbank.org/indicator/MS.MIL.XPND.GD.ZS">
                WorldBank
                </a>
                .<br></br>
                <br></br>
                Through comparing these two zoomable circle packings, we can find that 
                Asian countries spend more on their military but less on nuclear deployment. 
                In other word, they prefer other military deployments to nuclear warheads. 
                By continents, Europe spend most on nuclear deployment. 
                By country, the United States spend most on nuclear deployment and the deployment of 
                nuclear weapons makes a very important part of their military spending.
                By the type of nuclear weapons and countries, Russia and the United States have 
                a large number of warheads in their military stockpiles, which is far more than any other country.
              </Text>
            </Flex>
          </Flex>
          <Flex mr={120} ml={120} mt={-4}>
            <Flex w="50%" mr={10}>
              <Bubble></Bubble>
            </Flex>
            <Flex w="50%" ml={120}>
              <Bubble2></Bubble2>
            </Flex>
          </Flex>
        </>
        )}
        {activeStep === 6 && (    
          <>
          <Flex mr={120} ml={120} mt={-5}>
          <Flex w="100%" ml={-3}>
          <Text style={{ fontSize: "19px" }}>
                 This choropleth map represents the number of weapons in several countries such as China, Franch, India, United States
                 and so on. 
                 This data shows the nuclear weapon inventories from 1945 to 2020. We can observe the number of weapons in this country through the specific part of the color. The darker the color,
                 the more weapons this country have. We can easily see that United States and Russia have much more weapons than any other 
                 countries. 
                 <br/><br/>
                 In 1990s, both United States and Russia had plenty of nuclear weapon inventories. As the time goes by, the number of weapons in these two countries has decreased. Weapons in other countries such as 
                 India and Pakistan increased a little bit. 
               </Text>
            </Flex>
          </Flex>
          <Flex mr={120} ml={120} mt={5}>
            <Flex w="100%">
              <MapChart></MapChart>
            </Flex>
          </Flex>
        </>
        )}
        {activeStep === 5 && (
        <>
          <Flex mr={120} ml={120} mt={-5}>
          <Flex w="100%" ml={-3}>
              <Text style={{ fontSize: "19px" }}>
                We can intuitively observe the number of nuclear weapons tests from 1945 to 1998 by countries in this Mapbox. 
                Adjusting the time slider, the circles that point out countries' locations and the number of nuclear weapons tests will be changed from year to year.
                <br></br>
                <br></br>
                From the map, we can find that the Cold War was a very active period of nuclear weapons 
                development. The tests happened frequently in the Cold War, especially in Russia and the 
                United States, who were the hegemons of the Cold War. With the development of the Asian economy and 
                military force, the tests in China, India and Pakistan gradually became active. After the 1900s,
                the era of peace, the number of nuclear weapons tests decreased a lot. 
                We obtained this data from the{" "}
                <a href="https://docs.mapbox.com/playground/geocoding/">
                  Mapbox Geocoding API
                </a>
                and 
                {" "}
                <a href="https://ourworldindata.org/nuclear-weapons">
                  Our World In Data
                </a>
                .
              </Text>
            </Flex>
          </Flex>
          <Flex mr={120} ml={120} mt={5}>
            <Flex w="100%">
              <MapBox></MapBox>
            </Flex>
          </Flex>
        </>
        )}
      </Flex>
    </ThemeProvider>
  );
}

export default App;
