import React, { useRef, useEffect} from "react"
import mapboxgl from "mapbox-gl"
// import the mapbox styles
// alternatively can use a link tag in the head of public/index.html
// see https://docs.mapbox.com/mapbox-gl-js/api/
import "mapbox-gl/dist/mapbox-gl.css"
import "./mapbox.css";
import test from "./data/mapbox_data.geojson";



const App = () => {
  const mapContainer = useRef()

  const [value, setValue] = React.useState([1944, 1998]);

  const handleChange = (event, newValue) => {
      setValue(newValue);  
  };

  // this is where all of our map logic is going to live
  // adding the empty dependency array ensures that the map
  // is only rendered once
  useEffect(() => {
    mapboxgl.accessToken =
      "pk.eyJ1IjoieWppbjgzOTUiLCJhIjoiY2t2MDF0MHJ4NW1vdzJub2Y0MGFvM2dsZyJ9._iAJUX9MEMnSdR7KbjZnng";
    // create the map and configure it
    // check out the API reference for more options
    // https://docs.mapbox.com/mapbox-gl-js/api/map/
    const map = new mapboxgl.Map({
      container: mapContainer.current,
      style: 'mapbox://styles/mapbox/light-v10',
      center:  [31.4606, 20.7927],
      zoom: 1
    })

      map.on('load', () => {

        let filterYear = ['==', ['get', 'Year'], 1945];
        
        map.addSource('markers', {
          "type": "geojson",
          "data": test});
          
        map.addLayer({
          "id": "circles1",
          "source": "markers",
          "type": "circle",
          "paint": {
            'circle-radius': [
              'step',
              ['get', 'count'],
              0,
              0,
              5,
              1,
              10,
              3,
              20,
              5,
              30,
              10,
              40,
              20,
              50
              ],
              "circle-color": "#007cbf",
              "circle-opacity": 0.5,
              "circle-stroke-width": 0,
          },
      })

      map.addLayer({
        'id': 'labels',
        'type': 'symbol',
        'source': 'markers',
        'layout': {
        'text-field': ['concat', ['to-string', ['get', 'count']]],
        'text-font': ['Open Sans Bold', 'Arial Unicode MS Bold'],
        'text-size': 12
        },
        'paint': {
        'text-color': 'rgba(0,0,0,0.5)'
        }
        });

        document.getElementById('slider').addEventListener('input', (event) => {
          const year = parseInt(event.target.value, 10);
          // update the map
          filterYear = ['==', ['get', 'Year'], year];
          map.setFilter('circles1', filterYear);
          map.setFilter('labels', filterYear);
          document.getElementById('active-year').innerText = year
          });
          
      });
    // cleanup function to remove map on unmount
    return () => map.remove()
  }, [])
  
  

  return (
    <><div class="map-overlay top">
      <div class="map-overlay-inner">
        <h2>Year: <label id="active-year"></label></h2>
        <input id="slider" type="range" min="1945" max="1998" step="1" value={value}
        onChange={handleChange} ></input>
      </div>
        </div>
        <div ref={mapContainer} style={{ width: "100%", height: "100vh" }} />
    </>)
}

export default App
