import * as d3 from "d3";
import React, { Component} from "react";
import "./bar.css";

const data = require('./data/bar.json')


class BarChart extends Component {
    componentDidMount() {
        this.drawChart();
    }

    drawChart() {
        var margin = { top: 70, left: 120, bottom: 80, right: 50 };
        var width = 850 - margin.left - margin.right;
        var height = 500 - margin.top - margin.bottom;
        var x = d3.scaleBand();
        var y = d3.scaleLinear();
        var dataset, all, sort;
        var xAxis, yAxis;


        var svg = d3.select("#chart").append("svg")
            .attr("viewBox", `0 0 820 500`)
            .append('g')
            .attr('transform', 'translate(' + margin.left + ', ' + margin.top + ')');


        all = data;
        dataset = data;
        dataset.sort(function (a, b) { return d3.ascending(a.year, b.year); });
        drawBars();
        sort = "alphabetical";




        d3.select("#reset")
            .on("click", function () {
                dataset = all;
                sort = "alphabetical";
                x.domain(dataset.sort(
                    function (a, b) { return d3.ascending(a.year, b.year); }
                ).map(function (d) { return d.year; }));
                redraw();
            });
        d3.select("#alphabetical")
            .on("click", function () {
                sort = "alphabetical";
                x.domain(dataset.sort(
                    function (a, b) { return d3.ascending(a.year, b.year); }
                ).map(function (d) { return d.year; }));
                transition();
            });
        d3.select("#ascending")
            .on("click", function () {
                sort = "ascending";
                x.domain(dataset.sort(
                    function (a, b) { return d3.ascending(a.data, b.data); }
                ).map(function (d) { return d.year; }));
                transition();
            });
        d3.select("#descending")
            .on("click", function () {
                sort = "descending";
                x.domain(dataset.sort(
                    function (a, b) { return d3.descending(a.data, b.data); }
                ).map(function (d) { return d.year; }));
                transition();
            });


        d3.select("#all")
            .on("click", function () {
                dataset = all.sort(
                    function (a, b) { return d3.descending(a.data, b.data); }
                ).slice(0, 18);

                if (sort === "alphabetical") {
                    dataset.sort(function (a, b) { return d3.ascending(a.year, b.year); })
                } else if (sort === "ascending") {
                    dataset.sort(function (a, b) { return d3.ascending(a.data, b.data); })
                } else if (sort === "descending") {
                    dataset.sort(function (a, b) { return d3.descending(a.data, b.data); })
                }
                redraw();
            });

        d3.select("#top5")
            .on("click", function () {
                dataset = all.sort(
                    function (a, b) { return d3.descending(a.data, b.data); }
                ).slice(0, 5);

                if (sort === "alphabetical") {
                    dataset.sort(function (a, b) { return d3.ascending(a.year, b.year); })
                } else if (sort === "ascending") {
                    dataset.sort(function (a, b) { return d3.ascending(a.data, b.data); })
                } else if (sort === "descending") {
                    dataset.sort(function (a, b) { return d3.descending(a.data, b.data); })
                }
                redraw();
            });

        d3.select("#bottom5")
            .on("click", function () {
                dataset = all.sort(
                    function (a, b) { return d3.descending(a.data, b.data); }
                ).slice(5, 10);

                if (sort === "alphabetical") {
                    dataset.sort(function (a, b) { return d3.ascending(a.year, b.year); })
                } else if (sort === "ascending") {
                    dataset.sort(function (a, b) { return d3.ascending(a.data, b.data); })
                } else if (sort === "descending") {
                    dataset.sort(function (a, b) { return d3.descending(a.data, b.data); })
                }
                redraw();
            });


        function redraw() {

            x.domain(dataset.map(function (d) { return d.year; }));

            var bars = svg.selectAll(".bar")
                .data(dataset, function (d) { return d.year; });

            bars.enter().append("rect")
                .attr("x", function (d) { return x(d.year); })
                .attr("y", function (d) { return y(d.data); })
                .merge(bars)
                .transition()
                .duration(750)
                .attr("class", "bar")
                .attr("x", function (d) { return x(d.year); })
                .attr("y", function (d) { return y(d.data); })
                .attr("width", x.bandwidth())
                .attr("height", function (d) { return height - y(d.data); });


            bars.exit()
                .transition()
                .duration(750)
                .style("opacity", 0)
                .remove();


            var labels = svg.selectAll(".label")
                .data(dataset, function (d) { return d.data; });


            labels.transition()
                .duration(750)
                .attr("x", function (d) { return x(d.year) + x.bandwidth() / 2; });

            labels.enter().append("text")
                .attr("x", function (d) { return x(d.year) + x.bandwidth() / 2; })
                .attr("y", function (d) { return y(d.data) + (height - y(d.data)) / 2; })
                .style("opacity", 0)
                .transition()
                .duration(750)
                .text(function (d) { return d.data; })
                .attr("class", "label")
                .attr("x", function (d) { return x(d.year) + x.bandwidth() / 2; })
                .attr("y", function (d) { return y(d.data) + (height - y(d.data)) / 2; })
                .style("opacity", 1);


            labels.exit()
                .transition()
                .duration(750)
                .style("opacity", 0)
                .remove();


            xAxis = d3.axisBottom()
                .scale(x);
            svg.select(".x_axis")
                .transition()
                .duration(750)
                .call(xAxis)
                .selectAll('text')
                .attr('text-anchor', 'middle')
                .attr('transform', 'rotate(0)');
            svg.select(".y_axis")
                .call(yAxis);

        }

        function transition() {
            var transition = svg.transition()
                .duration(750);

            transition.selectAll(".bar")
                .attr("x", function (d) {
                    return x(d.year);
                });

            transition.selectAll(".label")
                .attr("x", function (d) {
                    return x(d.year) + x.bandwidth() / 2;
                });

            transition.select(".x_axis").call(xAxis);
            transition.select(".y_axis").call(yAxis);

            transition.select(".title")
                .text("The Number of Deaths Due to Weapons In War Each Year");
        }

        function drawBars() {
            x.domain(dataset.map(function (d) { return d.year; }))
                .range([0, width])
                .padding(0.05);

            y.domain([0, d3.max(dataset, function (d) { return d.data; })])
                .range([height, 0]);

            svg.selectAll(".bar")
                .data(dataset, function (d) { return d.year; })
                .enter().append("rect")
                .attr("class", "bar")
                .attr("x", function (d) { return x(d.year); })
                .attr("y", function (d) { return y(d.data); })
                .attr("width", x.bandwidth())
                .attr("height", function (d) { return height - y(d.data); });
            
            svg.selectAll(".label")
                .data(dataset, function (d) { return d.data; })
                .enter().append("text")
                .text(function (d) { return d.data; })
                .attr("class", "label")
                .attr("x", function (d) { return x(d.year) + x.bandwidth() / 2; })
                .attr("y", function (d) { return y(d.data) + (height - y(d.data)) / 2; })
                .style("opacity", 1);
 


            xAxis = d3.axisBottom()
                .scale(x);

            svg.append("g")
                .attr("class", "x_axis")
                .attr("transform", "translate(0," + height + ")")
                .call(xAxis)
                .selectAll('text')
                .attr('text-anchor', 'middle')
                .attr('transform', 'rotate(0)');

            svg.append("text")
                .attr("class", "xlabel")
                .text("Year")
                .attr("x", width / 2)
                .attr("y", height + 70)
                .style("baseline-shift", "nomal")


            yAxis = d3.axisLeft()
                .scale(y)
                .ticks(5, 'd');

            svg.append("g")
                .attr("class", "y_axis")
                .call(yAxis);

            svg.append("text")
                .attr('class', 'ylabel')
                .text("Number of Deaths")
                .attr("x", - height / 2)
                .attr("y", - margin.left * 0.7)
                .attr("transform", "rotate(-90)")
                .style("baseline-shift", "nomal")


            svg.append("text")
                .attr("class", "title")
                .text("The Number of Deaths Due to Weapons In War Each Year")
                .attr("x", width / 2)
                .attr("y", -20)
                .style("baseline-shift", "nomal")
         
        }
    }

    render() {
        return (

            <div>
                <script src="../../node_modules/highlightjs/highlight.pack.min.js"></script>
                <script>hljs.initHighlightingOnLoad();</script>
                <script src="../../node_modules/d3/dist/d3.min.js"></script>
                <span className="filter" id="reset">RESET</span>
                <span className="filter" id="ascending">Order Ascending by Value</span>
                <span className="filter" id="descending">Order Descending by Value</span>
                <span className="filter" id="all">All</span>
                <span className="filter" id="top5">Filter Top 5 by Value</span>
                <span className="filter" id="bottom5">Filter Bottom 5 by Value</span>
                
                <div className="container" id="chart">

                </div>

            </div>
        );
    }
}


export default BarChart;
