import React, {Component} from 'react';
import { feature } from 'topojson-client';
import axios from 'axios';
import { geoKavrayskiy7 } from 'd3-geo-projection';
import { geoGraticule, geoPath } from 'd3-geo';
import { select as d3Select } from 'd3-selection';
import { scaleLinear } from "d3-scale";
import ReactSlider from 'react-slider';
import * as d3 from "d3";
import { Info } from '@material-ui/icons';
import { yellow } from '@material-ui/core/colors';


const width = 960;
const height = 440;




class MapChart extends Component {

    
  
    constructor(){
        super();
        this.state = {
            map: null,
            value: [1945,2020]
        }
        this.refMap = React.createRef();
        this.handleChange = this.handleChange.bind(this);
    }

    
    // use handle change to adjust slider
    handleChange = (event, value) => {
      console.log("this is the value", value);
      this.setState({
        value: value
      });
    }

    componentDidMount() {
      
      // use url to create world map
      const WORLD_MAP_URL = "https://unpkg.com/world-atlas@1/world/110m.json";
        axios.get(WORLD_MAP_URL)
            .then(res => {
                const { data } = res;
                const land = feature(data, data.objects.countries).features;
                this.generateMap(land);
            })
            .catch(e => console.log('err in fecth world map data ', e))
    }

    generateMap(land){

        // select the svg area
        // project data on map
        const projection = geoKavrayskiy7()
            .scale(170)
            .translate([width / 2, height / 2])
            .precision(.1);

        const graticule = geoGraticule();

        // set canvas width and height
        const canvas = d3Select(this.refMap.current)
            .attr("width", width)
            .attr("height", height);

        // create context form canvas
        let context = canvas.node().getContext("2d");

        let path = geoPath()
            .projection(projection)
            .context(context);

            // ele points to the feature which includes id, coordinates and I already console it
            land.forEach(ele => {
              console.log(ele);
              context.fillStyle = "#d7d7d7";  // background color
              context.strokeStyle = '#000'; // white border
              context.globalAlpha = 0.7;
              context.beginPath();
              path(ele);
              context.fill();
              context.stroke();
  
              context.strokeStyle = 'rgba(220, 220, 220, 0.1)';
              context.beginPath();
              path(graticule());
              context.lineWidth = 0.1;
              context.stroke();
  
              context.beginPath();
              context.lineWidth = 0.5;
              path(graticule.outline());
              context.stroke();
          })

        // set map color to every area
          var color = scaleLinear()
            .domain([0, 600])
            .range(["white", "#fe4486"]);
          var color2 = scaleLinear()
            .domain([0, 41000])
            .range(["#fe4486", "#db2c00"]);

            var weapon_info = {
              1945: [0, 0, 0, 0, 0, 0, 0, 0, 0, 2],
              1946: [0, 0, 0, 0, 0, 0, 0, 0, 0, 9],
              1947: [0, 0, 0, 0, 0, 0, 0, 0, 0, 13],
              1948: [0, 0, 0, 0, 0, 0, 0, 0, 0, 50],
              1949: [0, 0, 0, 0, 0, 0, 1, 0, 0, 170],
              1950: [0, 0, 0, 0, 0, 0, 5, 0, 0, 299],
              1951: [0, 0, 0, 0, 0, 0, 20, 0, 0, 438],
              1952: [0, 0, 0, 0, 0, 0, 50, 0, 0, 841],
              1953: [0, 0, 0, 0, 0, 0, 120, 0, 1, 1169],
              1954: [0, 0, 0, 0, 0, 0, 150, 0, 5, 1703],
              1955: [0, 0, 0, 0, 0, 0, 200, 0, 10, 2422],
              1956: [0, 0, 0, 0, 0, 0, 426, 0, 50, 3692],
              1957: [0, 0, 0, 0, 0, 0, 660, 0, 58, 5543],
              1958: [0, 0, 0, 0, 0, 0, 826, 0, 60, 7325],
              1959: [0, 0, 0, 0, 0, 0, 1088, 0, 78, 12685],
              1960: [0, 0, 0, 0, 0, 0, 1688, 0, 105, 18577],
              1961: [0, 0, 0, 0, 0, 0, 2424, 0, 155, 22011],
              1962: [0, 0, 0, 0, 0, 0, 3357, 0, 211, 25903],
              1963: [0, 0, 0, 0, 0, 0, 4289, 0, 256, 28732],
              1964: [1, 4, 0, 0, 0, 0, 5233, 0, 270, 29685],
              1965: [5, 32, 0, 0, 0, 0, 6153, 0, 274, 31577],
              1966: [20, 36, 0, 0, 0, 0, 7035, 0, 283, 31011],
              1967: [25, 36, 0, 2, 0, 0, 8490, 0, 353, 31903],
              1968: [35, 36, 0, 4, 0, 0, 9490, 0, 317, 29732],
              1969: [50, 36, 0, 6, 0, 0, 10672, 0, 306, 27685],
              1970: [75, 36, 0, 8, 1, 0, 11736, 0, 375, 26577],
              1971: [100, 45, 0, 11, 0, 0, 13500, 0, 410, 25011],
              1972: [130, 70, 0, 13, 0, 0, 14000, 0, 420, 26903],
              1973: [150, 116, 0, 15, 0, 0, 15000, 0, 500, 27732],
              1974: [170, 145, 0, 17, 0, 0, 17000, 0, 500, 28685],
              1975: [180, 188, 0, 20, 0, 0, 19000, 0, 500, 27577],
              1976: [180, 212, 0, 22, 0, 0, 22942, 0, 500, 25011],
              1977: [180, 228, 0, 24, 0, 0, 24442, 0, 500, 25903],
              1978: [190, 235, 0, 26, 0, 2, 26368, 0, 500, 24732],
              1979: [200, 235, 0, 29, 0, 8, 28188, 0, 500, 24685],
              1980: [205, 250, 0, 31, 1, 14, 30188, 0, 500, 23577],
              1981: [210, 274, 0, 31, 0, 0, 32500, 0, 500, 23011],
              1982: [216, 274, 0, 33, 0, 0, 33000, 3, 380, 22903],
              1983: [218, 280, 0, 35, 0, 0, 35000, 3, 350, 23732],
              1984: [220, 280, 0, 38, 0, 0, 36000, 3, 350, 23685],
              1985: [222, 360, 0, 40, 0, 0, 38000, 3, 354, 23577],
              1986: [224, 350, 0, 42, 0, 0, 40942, 4, 353, 23011],
              1987: [226, 420, 0, 44, 0, 0, 38442, 5, 353, 23903],
              1988: [228, 410, 0, 47, 0, 0, 36368, 6, 350, 23732],
              1989: [230, 410, 0, 49, 0, 0, 35188, 6, 350, 22685],
              1990: [232, 505, 0, 50, 1, 0, 32188, 0, 350, 21577],
              1991: [234, 540, 0, 56, 0, 0, 29500, 0, 350, 19011],
              1992: [234, 540, 0, 58, 0, 0, 26000, 0, 350, 13903],
              1993: [234, 520, 0, 60, 0, 0, 24000, 0, 250, 11732],
              1994: [234, 510, 0, 62, 0, 0, 21000, 0, 250, 10685],
              1995: [234, 500, 0, 62, 0, 0, 18000, 0, 234, 10577],
              1996: [234, 450, 0, 64, 0, 0, 15942, 0, 203, 11011],
              1997: [232, 450, 0, 66, 0, 0, 15442, 0, 203, 10903],
              1998: [232, 450, 3, 68, 0, 2, 14368, 0, 240, 10732],
              1999: [232, 450, 8, 70, 0, 8, 13188, 0, 240, 10685],
              2000: [232, 470, 13, 72, 1, 14, 12188, 0, 280, 10577],
              2001: [235, 350, 18, 74, 1, 20, 11152, 0, 280, 10526],
              2002: [235, 350, 23, 76, 1, 26, 10114, 0, 280, 10457],
              2003: [235, 350, 28, 78, 1, 32, 9076, 0, 280, 10027],
              2004: [235, 350, 33, 80, 1, 38, 8038, 0, 280, 8570],
              2005: [235, 350, 38, 80, 1, 44, 7000, 0, 280, 8360],
              2006: [235, 350, 43, 80, 1, 50, 6643, 0, 225, 7853],
              2007: [235, 350, 50, 80, 1, 60, 6286, 0, 225, 5709],
              2008: [235, 300, 60, 80, 1, 70, 5929, 0, 225, 5273],
              2009: [240, 300, 70, 80, 1, 80, 5527, 0, 225, 5113],
              2010: [240, 300, 80, 80, 1, 90, 5215, 0, 225, 5066],
              2011: [240, 300, 90, 80, 1, 100, 4858, 0, 225, 4897],
              2012: [240, 300, 100, 80, 1, 110, 4750, 0, 225, 4881],
              2013: [250, 300, 110, 80, 1, 115, 4650, 0, 225, 4804],
              2014: [250, 300, 100, 80, 1, 120, 4600, 0, 220, 4717],
              2015: [250, 300, 110, 80, 5, 125, 4500, 0, 215, 4571],
              2016: [260, 300, 120, 80, 10, 130, 4490, 0, 215, 4018],
              2017: [270, 300, 130, 80, 15, 140, 4300, 0, 215, 3822],
              2018: [280, 300, 140, 80, 20, 150, 4350, 0, 210, 3785],
              2019: [290, 300, 140, 80, 25, 150, 4330, 0, 205, 3805],
              2020: [350, 290, 150, 90, 35, 160, 4310, 0, 195, 3750]
            };

            // this part is to draw legend
            var data2 = [{"color":"#fff","value":0},{"color":"#fdcccf","value":200},
            {"color":"#ffaafe","value":400},{"color":"#ff88e3","value":600},
            {"color":"#ff66bb","value":2000},{"color":"#ff4486","value":4000},
            {"color":"#ff2247","value":6000},{"color":"#ff0200","value":8000},
            {"color":"#df2f00","value":10000}];

            var extent = d3.extent(data2, d => d.value);
            
            var padding = 9;
            var width1 = 1020;
            var innerWidth = width1 - (padding * 2);
            var barHeight = 8;
            var height1 = 28;

            var xScale = d3.scaleLinear()
                .range([0, innerWidth])
                .domain(extent);

            var xTicks = data2.filter(f => f.value % 50 === 0).map(d => d.value);

            var xAxis = d3.axisBottom(xScale)
                .tickSize(barHeight * 2)
                .tickValues(xTicks);

            var svg = d3.select("#legend").attr("width", width1).attr("height", height1);
            var g = svg.append("g").attr("transform", "translate(" + padding + ", 0)");

            var defs = svg.append("defs");
            var linearGradient = defs.append("linearGradient").attr("id", "myGradient");
            linearGradient.selectAll("stop")
                .data(data2)
              .enter().append("stop")
                .attr("offset", d => ((d.value - extent[0]) / (extent[1] - extent[0]) * 100) + "%")
                .attr("stop-color", d => d.color);

            g.append("rect")
                .attr("width", innerWidth)
                .attr("height", barHeight)
                .style("fill", "url(#myGradient)");

            g.append("g")
                .call(xAxis)
              .select(".domain").remove();

            // from here we pass year as value, because the color is changing by the year
            document.getElementById('slider').addEventListener('input', (event) => {
              console.log("this event: ", event.target.value);
              const year = parseInt(event.target.value, 10);
              // filterYear = ['==', ['get', 'Year'], year];
             // map.setFilter('circles1', filterYear);
             // map.setFilter('labels', filterYear);
              document.getElementById('active-year').innerText = year
              
              // I put color based on the year and value
              const generateColor = (data) => {
                if (data.id === '156') {                 // id: 156; country: China
                  return color(weapon_info[year][0]); 
                } else if (data.id === '250') {          // id: 250; country: France
                  return color(weapon_info[year][1]);
                } else if (data.id === '356') {          // id: 356; country: India
                  return color(weapon_info[year][2]);
                } else if (data.id === '376') {          // id: 376; country: Israel
                  return color(weapon_info[year][3]);
                } else if (data.id === '408') {          // id: 408; country: North Korea
                  return color(weapon_info[year][4]);
                } else if (data.id === '586') {          // id: 586; country: Pakistan
                  return color(weapon_info[year][5]);
                } else if (data.id === '643') {  
                  if (weapon_info[year][6] < 600) {
                    return color(weapon_info[year][6]);
                  }       // id: 643; country: Russia
                  return color2(weapon_info[year][6]);
                } else if (data.id === '710') {          // id: 710; country: South Africa
                  return color(weapon_info[year][7]);
                } else if (data.id === '826') {          // id: 826; country: UK
                  return color(weapon_info[year][8]);
                } else if (data.id === '840') { 
                  if (weapon_info[year][9] < 600) {
                    return color(weapon_info[year][9]);
                  }         // id: 840; country: US
                  return color2(weapon_info[year][9]);
                } else {
                  return '#d7d7d7';
                }
                
              };
      
              land.forEach(ele => {
                
                  // draw colors on every area by using generateColor function
                  console.log(ele);
                  context.fillStyle = generateColor(ele);
                  console.log(generateColor(ele));
                  
                  context.strokeStyle = '#000';
                  context.globalAlpha = 0.7;
                  context.beginPath();
                  path(ele);
                  context.fill();
                  context.stroke();
      
                  context.strokeStyle = 'rgba(220, 220, 220, 0.1)';
                  context.beginPath();
                  path(graticule());
                  context.lineWidth = 0.1;
                  context.stroke();
      
                  context.beginPath();
                  context.lineWidth = 0.5;
                  path(graticule.outline());
                  context.stroke();
              })

            });
    }

    render() {
        return (
            <div className="map-box">
                <span><svg id="legend" width="100" hight="80"></svg></span>
                <span><canvas className="map" ref={this.refMap} /></span>
                <h2>Year: <label id="active-year"></label></h2>
                <input id="slider" type="range" min="1945" max="2020" step="1" value={this.state.value}
                onChange={this.handleChange} ></input>
            </div>
        );
    }
}

export default MapChart;
