import * as d3 from "d3";
import React, { Component, useEffect, useRef, useState } from "react";
import "./pie.css";
import voterTurnout from "./data/pie2.csv";

const data = []
d3.csv(voterTurnout, d => {
  data.push(d)
})



const PieChart = () => {
  const pie = useRef()
  useEffect(()=>{

    
    const piedata = d3.pie().value(d=>d.data)(data)
    const arc = d3.arc().innerRadius(0).outerRadius(200)
    const colors = d3.scaleOrdinal(d3.schemeCategory10)

    const tooldiv = d3.select('#chartArea2').append('span').style('visibility', 'hidden')

    const svg = d3.select('#chartArea2')
                  .append('svg')
                  .attr('background-color','#f8f8f8')
                  .attr('class', 'pie')
                  .attr('width', 500)
                  .attr('height',500);
        
    const g = svg.append('g').attr('transform', 'translate(' + (500/2) + ',' + (500/2) + ')');
        
    g.selectAll('path')
            .data(piedata).join('path')
            .attr('d', arc).attr('fill', (d,i)=>colors(i)).attr('stroke', 'white')
            .on('mouseover', (e,d)=>{
              tooldiv.style('visibility', 'visible').text(`${d.data.country}: ` + `${d.data.data}, ` + `${d.data.percentage}`)
            }).on('mousemove', (e,d)=>{
              tooldiv.style('top', (e.pageY-50) + 'px').style('left', (e.pageX-50) +'px')
            }).on('mouseout', ()=>{
              tooldiv.style('visibility', 'hidden')
            })

  })
  return (
    <div>
        <h2 style={{ padding: "10px 20px", textAlign: "center", fontSize: "19px" }}>1970 Global Nuclear Warhead Inventories pie chart</h2>
        <div style={{ padding: "10px 20px", textAlign: "center"}} id="chartArea2">
        </div>
    </div>
   
  )
}
export default PieChart;      
        
